<script>
   /**************************************************************/
   /* Svelte component for OutcomesPlot settings                 */
   /**************************************************************/

   import Container from '../../../shared/controls/Container.svelte';
   import Select from '../../../shared/controls/Select.svelte';
   import Range from '../../../shared/controls/Range.svelte';

   export let params;
   export let outcomes;
   export let intExcluded;

   let ncomp = 1;
   let showLabels = 'hide';
   let fom = 'eff';
   let fomView = 'val';
   let plotType = 'intervals';
   let zoom = 'off';


   $: params = {
      ncomp,
      showLabels,
      fom,
      fomView,
      plotType,
      zoom
   }

   let plotTypeOptions;
   $: if (!outcomes) {
      plotTypeOptions = intExcluded.length > 0  ? ['intervals', 'progress']   // no outcomes but at least one interval is excluded
                                                : null;                       // no outcomes, not intervals excluded, do not show the options
   } else if (outcomes.status === 'done') {
      plotTypeOptions = intExcluded.length > 0  ? ['intervals', 'statistics', 'progress']   // outcomes computed and at least one interval isexcluded
                                                : ['intervals', 'statistics'];              // outcomes computed but no intervals excluded yet
   } else {
      plotTypeOptions = null; // the selection is in progress, do not show the options
   }


   $: if (outcomes && outcomes.status === 'done' && ncomp > outcomes.ncomp) {
      ncomp = outcomes.ncomp;
   }

   $: if (intExcluded.length > 0 && outcomes === null && plotType === 'statistics') {
      plotType = 'progress';
   }

   $: if (outcomes && outcomes.status === 'running') {
      plotType = 'intervals';
   }
</script>

<Container label="">
   <p class="title">Outcomes plot</p>
</Container>

{#if plotTypeOptions}
   <Container label='Plot:'>
      <Select id='plotType' bind:value={plotType} options={plotTypeOptions} />
   </Container>
{/if}

{#if outcomes}
   {#if outcomes.status === 'running'}
      <Container label=''><p>Selection in progress...</p></Container>
   {/if}

   {#if outcomes.status === 'done'}
      <Container label='Statistic:'>
         <Select id='fom' bind:value={fom} options={['sens', 'spec', 'eff', 'sel']} />
      </Container>

      {#if plotType !== 'progress'}
      <Container label='# PCs:'>
         <Range id='ncomp' bind:value={ncomp} min={1} max={outcomes.ncomp} step={1} decNum={0} />
      </Container>
      {:else}
      <Container label='Zoom:'>
         <Select id='zoom' bind:value={zoom} options={['off', 'on']} />
      </Container>
      {/if}

      {#if plotType === 'intervals'}
      <Container label='Show:'>
         <Select id='fomVew' bind:value={fomView} options={['diff', 'val']} />
      </Container>

      {/if}
   {/if}

{:else}

   {#if plotType === 'progress'}
   <Container label='Statistic:'>
      <Select id='fom' bind:value={fom} options={['sens', 'spec', 'eff', 'sel']} />
   </Container>
   <Container label='Zoom:'>
      <Select id='zoom' bind:value={zoom} options={['off', 'on']} />
   </Container>
   {:else}
   <Container label=''><p>Start selection.</p></Container>
   {/if}

{/if}
