<script>
   /**************************************************************/
   /* AppContainer for variable selection                        */
   /* -------------------------                                  */
   /* app container for variable selection in SIMCA model        */
   /**************************************************************/


   import { getCurrentRes, getStat } from '../utils';
   import { seq } from '../../../shared/utils.js';

   import DataLoader from '../../../shared/components/PCADataLoader.svelte';

   import AppPlots from './AppPlots.svelte';
   import PCVCalculator from './PCVCalculator.svelte';
   import ModelCalculator from './ModelCalculator.svelte';
   import LOVE from '../shared/LOVE.svelte';
   import ResultTable from '../shared/ResultTable.svelte';
   import DownloadsComponent from '../shared/DownloadsComponent.svelte';


   /*****************************************/
   /* Component parameters                  */
   /*****************************************/

   export let WASM; // WASM object from parent app


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   let modelHash = null;
   let calset = undefined;             // calibration set
   let altset = undefined;             // calibration set
   let pvset = undefined;              // computed PV-set
   let datasets = undefined;           // object to keep all datasets together

   let resTarget = undefined;          // results for PV-set (global model)
   let resAlt = undefined;             // results for alt set (global model)

   let model = undefined;              // the global model
   let outcomes = undefined;           // object with outcomes for current interval models
   let varselparams = undefined;       // variable selection parameters
   let intExcluded = [];               // array with indices of excluded intervals
   let varsExcluded = [];              // array with indices of excluded variables
   let statsCurrent;                   // object with statistics for current model
   let statsProgress;                  // object with statistics for all selection steps
   let currentRes;                     // object with model and results obtained for the current model and selected 'ncomp' value
   let intSelected;

   let ncomp;                          // number of components selected by user
   let alpha;                          // significance level selected by user
   let oldNInt;

   let undoInProgress = false;         // this is needed to set the new current statistics values


   /**
    * Returns PCA and SIMCA results created using current selection of variables
    */
   async function getRes(WASM, model, datasets, varsExcluded, ncomp, alpha) {
      currentRes = await getCurrentRes(WASM, model, datasets, varsExcluded, ncomp, alpha);
   }

   /**
    * Removes the last selected interval from excluded and recalculated everything
    * @param e - event dispatched by AppPlots component with id of the selected point
    */
   function undoExcluded(e) {

      if (intExcluded.length < 2) return;

      // get last excluded interval
      const id = intExcluded[intExcluded.length - 1];

      // filter the outlier out
      if (id >= 0) {


         // get previos sate from statsProgress
         const lastInd =  statsProgress.ncomp.length - 1;

         // remove last value from stats progress
         statsProgress = {
            ncomp: statsProgress.ncomp.slice(0, lastInd),
            int: statsProgress.int.slice(0, lastInd),
            sens: statsProgress.sens.slice(0, lastInd),
            spec: statsProgress.spec.slice(0, lastInd),
            eff: statsProgress.eff.slice(0, lastInd),
            sel: statsProgress.sel.slice(0, lastInd)
         };

         // identify position of excluded variables corresponding to the interval and how many they are
         const left = varselparams.intLeftIndices[id];
         const right = varselparams.intRightIndices[id];
         const intLen = right === left ? 1 : right - left + 1;

         // remove excluded interval and variables from the list
         intExcluded = intExcluded.slice(0, intExcluded.length - 1);
         varsExcluded = varsExcluded.slice(0, varsExcluded.length - intLen);

         // adjust number of components
         ncomp = statsProgress.ncomp[lastInd - 1];

         // compute PCA/SIMCA results for the excluded intervals
         undoInProgress = true;
         currentRes = null;
         getRes(WASM, model, datasets, varsExcluded, ncomp, alpha);
      }
   }

   /**
    * Set the selected interval as excloded
    * @param e - event dispatched by AppPlots component with id of the selected point
    */
   function setExcluded(e) {

      // get position (index) of the sample to be removed
      const id = parseInt(e.detail.id);
      if (Number.isNaN(id) || id < 0 || id >= varselparams.nInt ) return;

      // filter the outlier out
      if (id >= 0 && (intExcluded.length === 0 || !intExcluded.includes(id))) {


         // if this is the first exclusion, save current ncomp as best to global statistics
         if (intExcluded.length === 0) {
            statsGlobal = {
               ncomp: statsGlobal.ncomp,
               ncompBest: ncomp,
               sens: statsGlobal.sens,
               spec: statsGlobal.spec,
               eff: statsGlobal.eff,
               sel: statsGlobal.sel,
            };
         }

         // save the statics of interval to be excluded as current model statistics
         statsCurrent = {
            ncomp: outcomes.ncomp,
            ncompBest: ncomp,
            sens: outcomes.sens.val.getrow(id + 1).v,
            spec: outcomes.spec.val.getrow(id + 1).v,
            eff: outcomes.eff.val.getrow(id + 1).v,
            sel: outcomes.sel.val.getrow(id + 1).v,
         };

         if (!statsProgress) {
            statsProgress = {
               ncomp: [ncomp],
               int: [id + 1],
               sens: [statsGlobal.sens[ncomp - 1]],
               spec: [statsGlobal.spec[ncomp - 1]],
               eff: [statsGlobal.eff[ncomp - 1]],
               sel: [statsGlobal.sel[ncomp - 1]]
            }
         };

         statsProgress = {
            ncomp: [...statsProgress.ncomp, ncomp],
            int: [...statsProgress.int, id + 1],
            sens: [...statsProgress.sens, statsCurrent.sens[ncomp - 1]],
            spec: [...statsProgress.spec, statsCurrent.spec[ncomp - 1]],
            eff: [...statsProgress.eff, statsCurrent.eff[ncomp - 1]],
            sel: [...statsProgress.sel, statsCurrent.sel[ncomp - 1]],
         };

         // exclude the interval and recompute the variable excluded array
         intExcluded = [...intExcluded, id];
         varsExcluded = varsExcluded.concat(seq(varselparams.intLeftIndices[id], varselparams.intRightIndices[id]));

         // compute PCA/SIMCA results for the excluded intervals and selected number of components
         getRes(WASM, model, datasets, varsExcluded, ncomp, alpha);
      }
   }

   /**
    * Reset arrays with excluded intervals and variables and get current model statistics
    * back to the global model.
    */
   function resetExcluded() {
      intExcluded = [];
      varsExcluded = [];
      statsCurrent = statsGlobal ? {
         ncompBest: null,
         ncomp: statsGlobal.ncomp,
         sens: statsGlobal.sens,
         spec: statsGlobal.spec,
         eff: statsGlobal.eff,
         sel: statsGlobal.sel,
      } : null;
      statsProgress = null;
   }


   // reactive expression to reset global model if calibration set was cancelled
   $: if (calset === null) {
      model = null;
   }

   // reactive expression to reset PV-set and target results if global model was canceled
   $: if (model === null) {
      pvset = null;
      resTarget = null;
   }

   // reactive expression to reset alternative set and corresponding results if PV-set was cancelled
   $: if (pvset === null) {
      altset = null;
      resTarget = null;
   }

   // reactive expression to reset alternative set results and all outcomes if alternative set was cancelled
   $: if (altset === null) {
      resAlt = null;
      outcomes = null;
      datasets = null;
      resetExcluded();
   } else if (!datasets && altset && calset && pvset) {
      datasets = {
         'cal': calset,
         'pv': pvset,
         'alt': altset,
      };
   }

   // reactive expression to compute statistics of global model (and current model as well which is at this stage is global model)
   let statsGlobal;
   $: if (resTarget && resAlt) {

      if (model && !statsGlobal) {
         // initialization
         const stat = getStat(model, resTarget, resAlt, null, alpha);
         statsGlobal = {
            ncomp: stat.ncomp,
            ncompBest: null,
            sens: stat.sens.val,
            spec: stat.spec.val,
            eff: stat.eff.val,
            sel: stat.sel.val,
         }
         statsCurrent = {
            ncomp: statsGlobal.ncomp,
            ncompBest: null,              // at this stage not best component is available
            sens: statsGlobal.sens,
            spec: statsGlobal.spec,
            eff: statsGlobal.eff,
            sel: statsGlobal.sel,
         };
         modelHash = model.hash;
      }
   } else {
      statsGlobal = null;
      statsCurrent = null;
   }


   // reactive expression to set current statistis/fom if undo was activated
   $: if (currentRes && undoInProgress) {
      undoInProgress = false;
      const ncompBest = currentRes.simca.ncomp;
      const sens = currentRes.pv.simcares.stat.sensitivity;
      const spec = currentRes.alt.simcares.stat.specificity;
      const sel = currentRes.alt.simcares.stat.selectivity;
      const eff = sens.map((v, i) => Math.sqrt(v * spec[i]));

      statsCurrent = {
         ncomp: sens.length,
         ncompBest: ncompBest,
         sens: new Float64Array(sens),
         spec: new Float64Array(spec),
         eff: new Float64Array(eff),
         sel: new Float64Array(sel),
      };
   }

   $: if (outcomes && outcomes.status !== 'done' && intSelected >= 0) {
      intSelected = -1;
   }

   $: if (varselparams && varselparams.nInt !== oldNInt) {
      oldNInt = varselparams.nInt;
      intSelected = -1;
      outcomes = null;
   }
</script>


<div class="app-container">

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="data-loader-train">
         <DataLoader bind:dataset={calset} {WASM} classMandatory={true} classUnique={true}/>
      </div>
      {#if calset }
      <div class="model-calculator">
         <ModelCalculator {WASM} {calset} bind:model={model}  />
      </div>
      {/if}
      {#if model }
      <div class="pcv-calculator">
         <PCVCalculator {WASM} {calset} {model} bind:pvres={resTarget} bind:pvset={pvset} />
      </div>
      {/if}
      {#if pvset }
      <div class="data-loader-alt">
         <DataLoader bind:dataset={altset} bind:res={resAlt} {model} {WASM} classMandatory={true} classUnique={false}/>
      </div>
      {/if}

      {#if statsGlobal}
      <div class="love-settings">
         <LOVE
            {WASM} {model} {datasets} {statsCurrent} {intExcluded} {varsExcluded}
            bind:outcomes={outcomes} bind:varselparams={varselparams} bind:alpha={alpha}
            on:resetexcluded={resetExcluded}
            on:undoexcluded={undoExcluded}
         />
      </div>
      {/if}

      {#if statsGlobal || statsCurrent}
      <div class="res-table">
         <ResultTable {statsGlobal} {statsProgress} {outcomes} {ncomp} {intSelected} />
      </div>
      <div class="downloads-widget">
         <DownloadsComponent {varsExcluded} {datasets} {currentRes} {statsProgress} />
      </div>
      {/if}

   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if !statsGlobal}
         <div class="help-text">
            <p>
               In this tab you can train a DD-SIMCA model and remove variables which are not important for classification. After you done with selection you can save it to a file and use it in other tabs. For example, validate and tune it using test set with target class members (tab <strong>Predict: Target</strong>) or apply it to a dataset with non-target class members (tab <strong>Predict: New</strong>).
            </p>
            {#if !calset}
            <p><strong>Step 1. Load training set from a CSV file.</strong></p>
            <p> The file should contain data as numeric values delimited
               by comma (with dot as decimal separator) or by semicolon (with comma as decimal separator). Rows correspond to observations or objects, columns  to variables.
            </p>
            <p>
               First column must contain class names for each row. All rows in the training set should belong to the target class, so it is expected that the class names are the same (you will get error message if not).
            </p>
            <p>
               First row can contain variable names (<strong>header</strong>) and first column can contain labels for the objects/observations (<strong>row labels</strong>)  just set the corresponding options after selecting the file. In this case it is assumed that column with the class names is located next to the column with row labels. Here is an example:
            </p>
            <div>
               <table>
               <tr>
                  <td>Name</td>
                  <td>Class</td>
                  <td>x1</td>
                  <td>x2</td>
                  <td>x3</td>
               </tr>
               <tr>
                  <td>O1</td>
                  <td>A</td>
                  <td>3.8</td>
                  <td>1.3</td>
                  <td>13.6</td>
               </tr>
               <tr>
                  <td>O2</td>
                  <td>A</td>
                  <td>11.1</td>
                  <td>12.3</td>
                  <td>3.3</td>
               </tr>
               <tr>
                  <td>O3</td>
                  <td>A</td>
                  <td>1.2</td>
                  <td>2.3</td>
                  <td>4.2</td>
               </tr>
               </table>
            </div>
            <p></p>
            {/if}

            {#if calset && !model}
            <p><strong>Step 2. Create a global PCA model</strong></p>
            <p>
               Next step is to create a global PCA model for the calibration set, which will be the basis for the SIMCA model. Use more components/latent variables (PCs) than you expect to be optimal. You will have possibility to set
               optimal number later.
            </p>
            {/if}

            {#if model && !pvset}
            <p><strong>Step 3. Compute PV-set</strong></p>
            <p>
               Now set necessary parameters for Procrustes cross-validation and click button to compute the PV-set, which will help you to optimize SIMCA model parameters. The parameters are the same as for conventional cross-validation: type of splits and number of segments. It is recommended to use fewer number of segments (e.g. 3, 4 or 5).
            </p>
            {/if}

            {#if model && pvset && !altset}
            <p><strong>Step 4. Load dataset with objects of alternative classes</strong></p>
            <p>
               Finally load test set with alternative classes members from a CSV file. The file should be organized in the same way as the training set.
            </p>
            <p>
               After this step you will be able to start variable selection procedure.
            </p>
            {/if}
         </div>
      {:else}
         <AppPlots
            {datasets} {varselparams} {outcomes} {intExcluded} {statsGlobal} {statsCurrent} {statsProgress}
            bind:ncomp={ncomp}
            bind:intSelected={intSelected}
            on:setexcluded={setExcluded}
         />
      {/if}
   </div>
</div>

