<script>
   /**************************************************************/
   /* --------------------                                       */
   /**************************************************************/

   import { Segments, TextLabels, Rectangles } from 'svelte-plots-basic/2d';

   // inputs
   export let limX;                 // x-axis limits (needed to set x-value for position of text labels)
   export let ncomp;
   export let statsGlobal;
   export let statsCurrent;
   export let outcomes;
   export let varselparams;
   export let fom = 'sens';
   export let fomView = 'diff';
   export let selected;
   export let seriesColors;
   export let fomKeys;

   // outputs
   export let yMin;                 // smallest y-value
   export let yMax;                 // largest y-value


   // coordinates of rectangles to show bar plots with statistics (and global value)
   let psAll, psMin, psMax, yDiff, globValue, bestValue, globXValue;
   $: if (fomView && outcomes && outcomes.status === 'done') {
      const values = (ncomp, outcomes[fom][fomView]);
      if (ncomp > values.ncols) ncomp = values.ncols;
      const x = varselparams.mid;
      const w = varselparams.width;
      const n = x.length;

      psMin = 0;
      psMax = 0;
      psAll = {};
      const bw = fomView === 'diff' ? 0.21 : 0.84;
      const wfactor = fomView === 'diff' ? 1 : 0;
      const bhw = 0.42;
      let nkey = 0;
      for (const key of fomKeys) {
         const y = outcomes[key][fomView].getcolumn(ncomp).v;

         const left = Array(n);
         const top = Array(n);
         const height = Array(n);
         const width = Array(n);

         for (let i = 0; i < n; i++) {
            left[i] = x[i] - bhw * w[i] + nkey * bw * wfactor * w[i],
            width[i] = bw * w[i];
            if (y [i] >= 0) {
               top[i] = y[i];
               height[i] = y[i];
               psMax = Math.max(psMax, y[i])
            } else {
               top[i] = 0;
               height[i] = -y[i];
               psMin = Math.min(psMin, y[i])
            }
         }

         psAll[key] = {left, top, height, width, faceColor: '#a8a04060'};
         nkey = nkey + 1;
      }
      yDiff = (psMax - psMin) * 0.20;
      yMin = psMin - yDiff;
      yMax = psMax + yDiff;
   } else {
      psMin = 0;
      psMax = 1;
      yMin = 0;
      yMax = 1.1;
      psAll = null;
      globValue = 0;
   }


   // coordinates of rectangle to show bar plots with statistics for selected interval
   let psSelAll, tlSelAll;
   $: if (ps && selected >= 0) {

      psSelAll = {};
      tlSelAll = {};
      for(const key of fomKeys) {
         const ps = psAll[key];
         const left = ps.left[selected];
         const width = ps.width[selected];
         const top = ps.top[selected];
         const height = ps.height[selected];
         const value = top === 0 ? -height : height;

         psSelAll[key] = {
            left:  [left],
            width: [width],
            top: [top],
            height: [height],
         }

         tlSelAll[key] = {
            xValues: [left + width * 0.5],
            yValues: [yMax - yDiff],
            labels: fomView === 'diff' ? [value.toFixed(3) + ' (' + key + ')'] : [value.toFixed(3)],
            pos: 3,
            textSize: 0.80,
            faceColor: seriesColors.selected
         }
      }
   } else {
      psSelAll = null;
      tlSelAll = null;
   }
   $: globValue = statsGlobal && fom ? statsGlobal[fom][statsGlobal.ncompBest ? (statsGlobal.ncompBest - 1) : ncomp - 1] : null;
   $: bestValue = statsCurrent && fom && statsCurrent.ncompBest ?
      statsCurrent[fom][statsCurrent.ncompBest ? (statsCurrent.ncompBest - 1) : ncomp - 1] : null;
   $: ps = psAll && fom ? psAll[fom] : null;
   $: globXValue = ps ? (limX[0] + ps.left[0]) * 0.5 : 0;
</script>

{#if psAll}

   {#if fomView === 'diff'}
      <!-- horizontal line at y = 0 -->
      <Segments xStart={[limX[0]]} xEnd={[limX[1]]} yStart={[0]} yEnd={[0]} lineColor='#808080'/>

      <!-- bar plots with difference in statistics for all foms -->
      {#each fomKeys as key}
      <Rectangles {...psAll[key]}
         lineWidth={0}
         faceColor={'#a8a040' + (key === fom ? 'e0' : '60')}
      />
      {/each}

      {#if psSelAll}
         {#each fomKeys as key}
           <Rectangles {...psSelAll[key]}
            lineWidth={0}
            faceColor={seriesColors.selected + (key === fom ? '' : '40')}
         />
         {/each}
      {/if}
   {:else}

      <!-- horizontal line at y = statistic for global model -->
      {#if globValue}
      <Segments xStart={[limX[0]]} xEnd={[limX[1]]} yStart={[globValue]} yEnd={[globValue]} lineColor={seriesColors.global}/>
      <TextLabels xValues={[globXValue]} yValues={[globValue]} labels={[globValue.toFixed(3)]} pos={globValue > bestValue ? 3 : 1} textSize={0.85} faceColor={seriesColors.global}/>
      {/if}

      <!-- horizontal line at y = statistic for best model -->
      {#if bestValue}
      <Segments xStart={[limX[0]]} xEnd={[limX[1]]} yStart={[bestValue]} yEnd={[bestValue]} lineType={3} lineColor={seriesColors.best}/>
      <TextLabels xValues={[globXValue]} yValues={[bestValue]} labels={[bestValue.toFixed(3)]}
         pos={globValue > bestValue ? 1 : 3} textSize={0.85} faceColor={seriesColors.best}/>
      {/if}

      <!-- bar plot with statistics for selected fom -->
      <Rectangles {...ps} faceColor='#a8a04080' />;
      {#if psSelAll}
         <Rectangles {...psSelAll[fom]} lineWidth={0} faceColor={seriesColors.selected} />
         <TextLabels {...tlSelAll[fom]} />
      {/if}

   {/if}
{/if}