<script>
    import { seq } from "../../../shared/utils";

   /******************************************************************/
   /* ResultsTable                                                   */
   /* -------------------------                                      */
   /* shows table with SIMCA-LOVE results global and current models  */
   /******************************************************************/

   // main properties of the whole app
   export let statsGlobal;
   export let statsProgress;
   export let outcomes;
   export let intSelected;
   export let ncomp;

   const MAX_RECORDS = 5;
   let lastRec = 0, firstRec, items;
   $: if (statsProgress) {
      lastRec = statsProgress.ncomp.length;
      firstRec = lastRec - MAX_RECORDS + 1;
      items = seq(firstRec <= 0 ? 0 : firstRec, lastRec - 1);
   } else {
      lastRec = 0;
      items = [];
   }

   let lastRow;
   $: if (outcomes && outcomes.status === 'done' && intSelected >= 0) {
      lastRow = {
         i: lastRec,
         ncomp: ncomp,
         int: intSelected + 1,
         sens: outcomes.sens.val.getcolumn(ncomp).v[intSelected],
         spec: outcomes.spec.val.getcolumn(ncomp).v[intSelected],
         eff: outcomes.eff.val.getcolumn(ncomp).v[intSelected],
         sel: outcomes.sel.val.getcolumn(ncomp).v[intSelected]
      };
   } else {
      lastRow = null;
   }
</script>


{#if statsGlobal && ncomp}
<table>
   <tr>
      <th>#</th>
      <th>int</th>
      <th>#PCs</th>
      <th>sens</th>
      <th>spec</th>
      <th>eff</th>
      <th>sel</th>
   </tr>
   {#if !statsProgress }
   <tr class="global">
      <td>0</td>
      <td>-</td>
      <td>{ncomp}</td>
      <td>{statsGlobal.sens[ncomp - 1].toFixed(3)}</td>
      <td>{statsGlobal.spec[ncomp - 1].toFixed(3)}</td>
      <td>{statsGlobal.eff[ncomp - 1].toFixed(3)}</td>
      <td>{statsGlobal.sel[ncomp - 1].toFixed(3)}</td>
   </tr>
   {:else}

   {#if firstRec > 0}
   <tr class="global">
      <td>{0}</td>
      <td>-</td>
      <td>{statsProgress.ncomp[0]}</td>
      <td>{statsProgress.sens[0].toFixed(3)}</td>
      <td>{statsProgress.spec[0].toFixed(3)}</td>
      <td>{statsProgress.eff[0].toFixed(3)}</td>
      <td>{statsProgress.sel[0].toFixed(3)}</td>
   </tr>
   <tr>
      <td>...</td>
      <td>...</td>
      <td>...</td>
      <td>...</td>
      <td>...</td>
      <td>...</td>
      <td>...</td>
   </tr>
   {/if}

   {#each items as i}
   <tr class:global={i==0} class:current={i === statsProgress.int.length - 1}>
      <td>{i}</td>
      <td>{i > 0 ? statsProgress.int[i] : '-'}</td>
      <td>{statsProgress.ncomp[i]}</td>
      <td>{statsProgress.sens[i].toFixed(3)}</td>
      <td>{statsProgress.spec[i].toFixed(3)}</td>
      <td>{statsProgress.eff[i].toFixed(3)}</td>
      <td>{statsProgress.sel[i].toFixed(3)}</td>
   </tr>
   {/each}

   {/if}

   {#if lastRow }
   <tr class="selected">
      <td>{lastRow.i}</td>
      <td>{lastRow.int}</td>
      <td>{ncomp}</td>
      <td>{lastRow.sens.toFixed(3)}</td>
      <td>{lastRow.spec.toFixed(3)}</td>
      <td>{lastRow.eff.toFixed(3)}</td>
      <td>{lastRow.sel.toFixed(3)}</td>
   </tr>
   {/if}

</table>
{/if}

<style>
   table {
      width: 100%;
      border-collapse: collapse;
      font-size: 0.85em;
      margin-top: 1em;
   }

   tr {
      width: 100%;
   }

   th {
      border-bottom: 1px solid #a0a0a0;
   }

   th, td {
      text-align: right;
      padding: 0.35em 0.5em 0.35em 0.1em;
      color: #404040;
      margin: 0;
   }

   td {
      color: #a0a0a0;
   }

   .label {
      text-align: left;
      padding: 0.1em 0.5em;
   }

   tr.global td{
      color: #404040;
   }

   tr.current td{
      color: #2679B2;
   }

   tr.selected td{
      color: #D22C2F;
   }
</style>