<script>
   /**************************************************************/
   /* SelectedIntervalPlot                                       */
   /* --------------------                                       */
   /* shows plot with statistics for selected interval           */
   /**************************************************************/

   import { Vector } from 'mdatools/arrays';
   import { Axes, XAxis, YAxis, Box, Lines, Points, Legend } from 'svelte-plots-basic/2d';
   import { PNG_WIDTH, PNG_HEIGHT, PNG_RES, MARGINS } from '../../../shared/plots/Defaults.js';

   export let params;                     // JSON with main plot parameters
   export let outcomes;                   // JSON with LOVE outcomes
   export let statsGlobal;
   export let statsCurrent;
   export let selected;       // selected variable
   export let seriesColors;

   export let fileName = 'ddsimca-love-models';
   export let pngWidth = PNG_WIDTH;
   export let pngHeight = PNG_HEIGHT;
   export let pngRes = PNG_RES;


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   const fomNames = {
      'sens': 'sensitivity',
      'spec': 'specificity',
      'eff': 'efficiency',
      'sel': 'selectivity'
   };

   // local copies of user defined plot parameters
   let fom = 'sens'
   let fomView = 'diff';

   // input dependent but permanent values
   let xLabel = 'PCs';
   let ncomp = 1;


   function getLegend(gs, bs, ss) {
      const items = [];

      if (gs) {
         items.push({
            label: 'global',
            borderColor: gs.lineColor,
            marker: 1,
         })
      }
      if (bs) {
         items.push({
            label: 'current',
            borderColor: bs.lineColor,
            marker: 1,
         })
      }
      if (ss) {
         items.push({
            label: 'selected ('  + (selected + 1) + ')',
            borderColor: ss.lineColor,
            marker: 1,
         })
      }

      return items;
   }

   // reactive expressions to monitor changes in plot parameters
   $: if (params && params.fom) fom = params.fom;
   $: if (params && params.fomView) fomView = params.fomView;
   $: if (params && params.ncomp) ncomp = params.ncomp;


   // reactive expressions for title, y-axis label and limits
   let title, yLabel, limY;
   $: title = 'Variable selection outcomes';
   $: yLabel = fomNames[fom];
   $: limY = [0, 1.1];

   // plot series for global model
   let gs_line, gs_points, gs_points_ncomp, limX = [0, 1];
   $: if (statsGlobal) {
      const nCompMax = statsGlobal[fom].length;
      const xValues = Vector.seq(1, nCompMax);
      const yValues = new Vector(statsGlobal[fom]);
      gs_line = {xValues, yValues, lineColor: seriesColors.global};
      gs_points = {xValues, yValues, borderColor: seriesColors.global, faceColor: '#fafafa'};
      gs_points_ncomp = {xValues: [ncomp], yValues: [yValues.v[ncomp - 1]], borderColor: seriesColors.global, faceColor: '#fafafa', borderWidth: 3};
      limX = [0, nCompMax + 1];
   } else {
      gs_line = null;
      gs_points = null;
      gs_points_ncomp = null;
   }

   // plot series for best model
   let bs_line, bs_points, bs_points_ncomp;
   $: if (statsCurrent) {
      const nCompMax = statsCurrent[fom].length;
      const xValues = Vector.seq(1, nCompMax);
      const yValues = new Vector(statsCurrent[fom]);
      bs_line = {xValues, yValues, lineColor: seriesColors.best};
      bs_points = {xValues, yValues, borderColor: seriesColors.best, faceColor: '#fafafa'};
      bs_points_ncomp = {xValues: [ncomp], yValues: [yValues.v[ncomp - 1]], borderColor: seriesColors.best, faceColor: '#fafafa', borderWidth: 3};
   } else {
      bs_line = null;
      bs_points = null;
      bs_points_ncomp = null;
   }


   // plot series for selected interval model
   let ss_line, ss_points, ss_points_ncomp;
   $: if (outcomes && selected >= 0) {
      const nCompMax = outcomes[fom][fomView].ncols;
      const xValues = Vector.seq(1, nCompMax);
      const yValues = outcomes[fom].val.getrow(selected + 1);
      ss_line = {xValues, yValues, lineColor: seriesColors.selected};
      ss_points = {xValues, yValues, borderColor: seriesColors.selected, faceColor: '#fafafa'}
      ss_points_ncomp = {xValues: [ncomp], yValues: [yValues.v[ncomp - 1]], borderColor: seriesColors.selected, faceColor: '#fafafa', borderWidth: 3}
   } else {
      ss_line = null;
      ss_points = null;
      ss_points_ncomp = null;
   }

   $: legendItems = getLegend(gs_line, bs_line, ss_line);
</script>


{#if params}
<Axes
   margins={MARGINS}
   downloadLinks="hover"
   {fileName}
   {xLabel}
   {yLabel}
   {limX}
   {limY}
   {title}
   {pngWidth}
   {pngHeight}
   {pngRes}
>

   {#if gs_line && gs_points}
   <Lines {...gs_line} />
   <Points {...gs_points} />
   <Points {...gs_points_ncomp} />
   {/if}

   {#if bs_line && bs_points}
   <Lines {...bs_line} />
   <Points {...bs_points} />
   <Points {...bs_points_ncomp} />
   {/if}

   {#if ss_line && ss_points}
   <Lines {...ss_line} />
   <Points {...ss_points} />
   <Points {...ss_points_ncomp} />
   {/if}

   <Legend items={legendItems} position='bottomright' />
   <Box slot="box" />
   <XAxis slot="xaxis" showGrid={true} />
   <YAxis slot="yaxis" showGrid={true} />
</Axes>
{/if}