<script>
   /******************************************************************/
   /* DownloadsComponent                                             */
   /* -------------------------                                      */
   /* lets user download main outcomes                               */
   /******************************************************************/

   import Container from '../../../shared/controls/Container.svelte';
   import Button from '../../../shared/controls/Button.svelte';
   import { downloadResults, downloadModel } from '../../../ddsimca/src/utils';
   import { downloadData, excludeVars } from '../../../shared/utils';


   /*****************************************/
   /* Component parameters                  */
   /*****************************************/

   // input
   export let currentRes;        // object with current model and results
   export let datasets;          // original training set
   export let varsExcluded;      // array with excluded variables
   export let statsProgress;     // selection progress statistics


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   /**
    * Creats a CSV string for table with progress statistics, turn it to Blob and start download process.
    *
    * @param {Object} statsProgress - JSON with statistics for every step.
    * @param {String} sep - character used to separate values.
    */
   function downloadLOVEHistory(statsProgress, sep, dec) {

      let out = ['#', 'excluded interval', 'nPCs', 'sensitivity', 'specificity', 'efficiency', 'selectivity'].join(sep) + '\n';
      const n = statsProgress.ncomp.length;
      for (let i = 0; i < n; i++) {
         out += [
            i,
            i === 0 ? '-' : statsProgress.int[i],
            statsProgress.ncomp[i],
            statsProgress.sens[i],
            statsProgress.spec[i],
            statsProgress.eff[i],
            statsProgress.sel[i],
         ].join(sep) + '\n';
      }

      // correct decimal separator if neccessary
      if (dec !== '.') {
         out = out.replace(/\./g, dec);
      }

      const blob = new Blob([out], { type: 'text/csv' });
      const url = window.URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.setAttribute('href', url)
      a.setAttribute('download', `ddsimca-love-progress.csv`);
      a.click()
   }

   /**
    * Let user to download the current results
    * @param name
    * @param currentRes
    */
   function downloadLOVEData(name, varsExcluded) {

      const dataset = datasets[name];
      if (!varsExcluded || varsExcluded.length === 0) {
         downloadData(dataset, `${dataset.fileName}`);
         return;
      }
      downloadData(excludeVars(dataset, varsExcluded), `${dataset.fileName}-reduced`);
   }

   /**
    * Let user to download the current results
    * @param name
    * @param currentRes
    */
   function downloadLOVEResults(name, currentRes, varsExcluded) {
      const res = currentRes[name].simcares;
      downloadResults(res, datasets[name], name, [], res.ncomp, varsExcluded);
   }

   /**
    * Let user to download the current results
    * @param name
    * @param currentRes
    */
   function downloadLOVEModel(currentRes) {
      downloadModel(currentRes.model, currentRes.simca, currentRes.simca.ncomp);
   }

</script>

{#if varsExcluded && varsExcluded.length > 0}
<Container label="">
   <p class="title">Download</p>
</Container>

<Container label='Progress'>
   <span class="buttons">
   <Button on:click={() => downloadLOVEHistory(statsProgress, datasets.cal.sep, datasets.cal.dec)}>Full selection progress</Button>
   </span>
</Container>
<Container label='Outcomes'>
   <span class="buttons">
   <Button on:click={() => downloadLOVEModel(currentRes)}>Model</Button>
   <Button on:click={() => downloadLOVEResults("pv", currentRes, varsExcluded)}>Res (pv)</Button>
   <Button on:click={() => downloadLOVEResults("alt", currentRes, varsExcluded)}>Res (alt)</Button>
   </span>
</Container>
<Container label='Reduced data'>
   <span class="buttons">
   <Button on:click={() => downloadLOVEData("cal", varsExcluded)}>Train</Button>
   <Button on:click={() => downloadLOVEData("pv", varsExcluded)}>PV</Button>
   <Button on:click={() => downloadLOVEData("alt", varsExcluded)}>Alt</Button>
   </span>
</Container>
<Container label='Original data'>
   <span class="buttons">
   <Button on:click={() => downloadLOVEData("cal")}>Train</Button>
   <Button on:click={() => downloadLOVEData("pv")}>PV</Button>
   <Button on:click={() => downloadLOVEData("alt")}>Alt</Button>
   </span>
</Container>
{/if}

<style>
   .buttons {
      position: relative;
      box-sizing: border-box;
      display: inline-block;
      flex: 1 1 auto;
      margin: 0;
      padding: 2px 0;
      width: auto;
      border-radius: 2px;
      cursor: default;
      white-space: nowrap;
   }
</style>