<script>
   /**************************************************************/
   /* ProgressPlot                                               */
   /* --------------------                                       */
   /* shows plot with statistics vs. selection steps             */
   /**************************************************************/

   import { range } from 'mdatools/stat';
   import { Axes, XAxis, YAxis, Box, Lines, Points } from 'svelte-plots-basic/2d';
   import { PNG_WIDTH, PNG_HEIGHT, PNG_RES, MARGINS } from '../../../shared/plots/Defaults.js';
   import { seq } from '../../../shared/utils.js';

   export let params;                     // JSON with main plot parameters
   export let statsProgress;
   export let selected;       // selected variable
   export let outcomes;
   export let ncomp;
   export let seriesColors;
   export let fomKeys;
   export let fomNames;


   export let fileName = 'ddsimca-love-progress';
   export let pngWidth = PNG_WIDTH;
   export let pngHeight = PNG_HEIGHT;
   export let pngRes = PNG_RES;


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // local copies of user defined plot parameters
   let fom = 'sens'
   let zoom = 'off';

   // input dependent but permanent values
   let xLabel = 'Steps';


   // reactive expressions to monitor changes in plot parameters
   $: if (params && params.fom) fom = params.fom;
   $: if (params && params.zoom) zoom = params.zoom;


   // reactive expressions for title, y-axis label and limits
   let title, yLabel, limY;
   $: title = 'Variable selection progress';
   $: yLabel = fomNames[fom];

   let ps, psLast, psFirst, psAll, n, limX = [0, 1], yMin, yMax;
   $: if (statsProgress && statsProgress.ncomp.length > 0) {
      n = statsProgress.ncomp.length;
      const xValues = seq(0, n-1);
      psAll = {};
      yMin = 1.1;
      yMax = 0;
      for (const key of fomKeys) {
         const v = statsProgress[key];
         const vr = range(v);
         yMin = Math.min(yMin, vr[0]);
         yMax = Math.max(yMax, vr[1]);
         psAll[key] = {
            xValues: xValues,
            yValues: statsProgress[key]
         }
      };
      ps = psAll[fom];
      psLast = {
         xValues: [n-1],
         yValues: [statsProgress[fom][n - 1]]
      };
      psFirst = {
         xValues: [0],
         yValues: [statsProgress[fom][0]]
      };
      limX = [-0.5, n - 0.5];
   } else {
      n = 1;
      ps = null;
      limX = [0, 1];
      yMin = 0;
      yMax = 1.1;
   }

   let ss, ssLine, ssMin, ssMax, ssAll;
   $: if (ps && outcomes && outcomes.status === 'done' && selected >= 0) {
      ssMin = 1;
      ssMax = 0;
      ssAll = {};
      for (const key of fomKeys) {
         const v = outcomes[key].val.getcolumn(ncomp).v[selected]
         ssMin = Math.min(ssMin, v);
         ssMax = Math.max(ssMax, v);
         ssAll[key] = {
            xValues: [n - 1, n],
            yValues: [psAll[key].yValues[n-1], v]
         }
      };

      ss = ssAll[fom];
      ssLine = {
         xValues: [n - 1, n],
         yValues: [psLast.yValues[0], ss.yValues[1]]
      };
      limX = [-0.5, n + 0.5];
   } else {
      ss = null;
      ssMin = 1;
      ssMax = 0;
      limX = [-0.5, n - 0.5];
   }

   $: limY = zoom === 'on' && ps && ps.yValues ? [Math.min(yMin, ssMin) - 0.02, Math.max(yMax, ssMax) + 0.02]: [0, 1.1];
</script>


{#if params}
<Axes
   margins={MARGINS}
   downloadLinks="hover"
   {fileName}
   {xLabel}
   {yLabel}
   {limX}
   {limY}
   {title}
   {pngWidth}
   {pngHeight}
   {pngRes}
>

   {#if ss}
   {#each Object.keys(ssAll) as v}
   <Lines {...ssAll[v]} lineColor='#d0d0d0' lineType={3} />
   <Points {...ssAll[v]} markerSize={0.75} borderColor='#d0d0d0' faceColor='#e0e0e0' />
   {/each}
   <Lines {...ssLine} lineColor={seriesColors.selected} lineType={3}  />
   <Points {...ss} borderColor={seriesColors.selected} faceColor={seriesColors.selected} borderWidth={2} />
   {/if}


   {#if ps}
   {#each Object.keys(psAll) as v}
   <Lines {...psAll[v]} lineColor='#d0d0d0'  />
   <Points {...psAll[v]} markerSize={0.75} borderColor='#d0d0d0' faceColor='#e0e0e0' />
   {/each}
   <Lines {...ps} lineColor='#808080'  />
   <Points {...ps} borderColor='#808080' faceColor='#fafafa' />
   <Points {...psLast} borderColor={seriesColors.best} borderWidth={3} />
   <Points {...psFirst} borderColor={seriesColors.global} borderWidth={3} />
   {/if}



   <Box slot="box" />
   <XAxis slot="xaxis" showGrid={true} whole={true}/>
   <YAxis slot="yaxis" showGrid={true} />
</Axes>
{/if}