import { Matrix, vector } from 'mdatools/arrays';
import { mean, variance } from 'mdatools/stat';
import { pnorm, qchisq } from 'mdatools/distributions';

import { pca_predict, pca_fit, pcares_get, pcamodel_get } from '../../shared/wasm/dist/pca.js';
import { simca_predict, simca_predict_alt } from '../../ddsimca/src/utils.js';
import { genhash } from '../../shared/utils.js';



/**
 * Cretes object with SIMCA results objects which have the same structure as the main DD-SIMCA app.
 * This is required to download the results.
 *
 * @param {Object} model - current PCA model.
 * @param {Object} datasets - JSON with all datasets.
 * @param {Array} varsExcluded - array with indices of the excluded variables.
 * @param {Number} ncomp - number of components selected by user when variable was excluded.
 * @param {Number} alpha - significance level for extremes.
 * @param {Number} alpha - significance level for outliers (needed for compatibility).
 * @returns {Object} JSON with two fields: 'pv' and 'app'. Each field contains JSON with PCA and SIMCA results.
 */
export async function getCurrentRes(WASM, model, datasets, varsExcluded, ncomp, alpha, gamma, limType) {

      if (!gamma) {
         gamma = 0.1;
      }

      if (!limType) {
         limType = 'classic';
      }

      const X_train = datasets.cal.values;
      const X_pv = datasets.pv.values;
      const X_alt = datasets.alt.values;
      const className = datasets.cal.classNames[0];

      // adjust number of components
      // here we take into account only variables as rows are taken in the global model
      const ncols = X_train.ncols - varsExcluded.length;
      const ncompMax = model.ncomp > ncols ? ncols : model.ncomp;

      return WASM()
         .then(Module => {
            const m = pca_fit(Module, X_train, ncompMax, model.center, model.scale, [], varsExcluded);

            const res_target = pca_predict(Module, m, X_pv);
            res_target['objindices'] = datasets.pv.objindices;
            res_target['objlabels'] = datasets.pv.objlabels;
            res_target['classes'] = datasets.pv.classes;
            res_target['classNames'] = datasets.pv.classNames;

            const res_alt = pca_predict(Module, m, X_alt);
            res_alt['objindices'] = datasets.alt.objindices;
            res_alt['objlabels'] = datasets.alt.objlabels;
            res_alt['classes'] = datasets.alt.classes;
            res_alt['classNames'] = datasets.alt.classNames;

            m['lim'] = {
               'classic': {
                  'h0': pcamodel_get(m, "ch0"),
                  'Nh': pcamodel_get(m, "cNh"),
                  'q0': pcamodel_get(m, "cq0"),
                  'Nq': pcamodel_get(m, "cNq"),
                  'g0': pcamodel_get(m, "cg0"),
                  'Ng': pcamodel_get(m, "cNg"),
                  'r0': pcamodel_get(m, "cr0"),
                  'Nr': pcamodel_get(m, "cNr")
               },
               'robust': {
                  'h0': pcamodel_get(m, "rh0"),
                  'Nh': pcamodel_get(m, "rNh"),
                  'q0': pcamodel_get(m, "rq0"),
                  'Nq': pcamodel_get(m, "rNq"),
                  'g0': pcamodel_get(m, "rg0"),
                  'Ng': pcamodel_get(m, "rNg"),
                  'r0': pcamodel_get(m, "rr0"),
                  'Nr': pcamodel_get(m, "rNr")
               }
            };
            m['hash'] = genhash();
            m['varlabels'] = datasets.cal.varlabels.filter(v => !varsExcluded.includes(v));
            m['varindices'] = datasets.cal.varindices.filter(v => !varsExcluded.includes(v));
            m['varvalues'] = datasets.cal.varvalues.filter(v => !varsExcluded.includes(v));
            m['varvaluesName'] = datasets.cal.varvaluesName;
            m['varvaluesUnits'] = datasets.cal.varvaluesUnits;
            m['varrev'] = datasets.cal.varrev
            m['header'] = datasets.header
            m['prep'] = [];

            const pe = 1 - alpha / 100;
            const poo = Math.pow(1 - gamma / 100, 1 / model.nrows);
            const pvo = Math.pow(1 - gamma / 100, 1 / model.ncols);

            const Nh = m.lim.classic.Nh;
            const h0 = m.lim.classic.h0;
            const Nq = m.lim.classic.Nq;
            const q0 = m.lim.classic.q0;
            const Nf = Nq.map((v, i) => v + Nh[i]);
            const eCrit = Nf.map(v => qchisq(pe, v));
            const oCrit = Nf.map(v => qchisq(poo, v));

            const Ng = m.lim.classic.Ng;
            const g0 = m.lim.classic.g0;
            const Nr = m.lim.classic.Nr;
            const r0 = m.lim.classic.r0;
            const Ni = Ng.map((v, i) => v + Nr[i]);
            const veCrit = Ni.map(v => qchisq(pe, v));
            const voCrit = Ni.map(v => qchisq(pvo, v));


            const simca = {
               ncomp: ncomp,
               nrows: model.nrows,
               ncols: model.ncols,
               alpha, gamma,
               limType: limType,
               g0, r0, Ng, Nr, Ni,
               h0, q0, Nh, Nq, Nf,
               eCrit, oCrit,
               veCrit, voCrit,
               className,
            };

            const out = {
               model: m,
               simca: simca,
               pv: {
                  pcares: res_target,
                  simcares: simca_predict(res_target, simca, [])
               },
               alt: {
                  pcares: res_alt,
                  simcares: simca_predict_alt(res_alt, simca)
               }
            };

            return out;
         })
         .catch(e =>{
            console.error(e)
         });
}


/**
 * Computes theoretical selectivity of outcomes.
 *
 * @param {Float64Array} f - array with full distance values.
 * @param {Number} fCrit - critical value for extremes.
 * @param {Number} k - number of degrees of freedom (Nf)
 * @returns
 */
export function getSelectivity(f, fCrit, k) {

   let I = f.length;

   const fv = vector(f);
   let fp = fv.sort();
   let ind = fv.sortind();

   // Step 2. If Disc <0
   let Disc = -1;
   let n = 0;
   let m = 0;
   let d = 0;
   let M1 = 0;

   while (Disc < 0) {
      if (n > 0) {
         I = I - 1;
         ind = ind.slice(1, I);
         fp = fp.slice(1, I);
      }
      m = mean(fp);
      d = variance(fp, true);
      M1 = d / (m * m);
      Disc = 4 - 2 * k * M1;
      n = n + 1;
   }

   // Step 3. Calculate  x  by Eq. (18)
   const x = (2 + Math.sqrt(Disc)) / M1;

   // 	Calculate  s and  f'0 by Eq. (19)
   const s = (x - k);
   const f0 = m / x;

   // 	Calculate: z, h, r, p, Mz, Sz by Eq. (20)
   const z = fCrit / f0;
   const h = 1 - 2 * (k + s) * (k + 3 * s) / (3 * (k + 2 * s)**2);
   const r = (h - 1) * (1 - 3 * h);
   const p = (k + 2 * s) / (k + s)**2;
   const Mz = 1 + h * p * (h - 1 - 0.5 * (2 - h) * r * p);
   const Sz = h * Math.sqrt(2 * p) * (1 + 0.5 * r * p);

   // Step 3.	If  is given then calculate  by Eq. (21)
   const beta = pnorm((Math.pow(z / (k + s), h) - Mz) / Sz);
   return 1 - beta;
}


/**
 * Returns JSON with sensitivity, specificity, efficiency and selectivity values
 * computed for current model and datasets. If statistics for best model are provided it also
 * computes the differences.
 *
 * @param {Object} m - JSON with PCA model.
 * @param {Object} rTarget - PCA results computed for target class (PV-set)
 * @param {Object} rAlt - PCA results computed for alternative classs (Alternative set)
 * @param {Object} statsCurrent - JSON with sensitivity, specificity, efficiency and selectivity of a current model.
 * @param {Number} alpha - the significance level.
 *
 * @returns {Object} JSON object for every statistic with two fields: 'val' with statistic value and
 * 'diff' difference between the value and the corresponding statistic from the current model. If the
 * current model statistics are not provided, 'diff' and 'val' are the same.
 */
export function getStat(m, rTarget, rAlt, statsCurrent, alpha) {

   if (!alpha) alpha = 5;

   if (alpha < 0.01 || alpha > 50) {
      throw new Error("getStat: wrong value for 'alpha' parameter.");
   }

   let sensCurrent, specCurrent, effCurrent, selCurrent, ncompBest;
   if (!statsCurrent) {
      sensCurrent = new Float64Array(m.ncomp);
      specCurrent = new Float64Array(m.ncomp);
      effCurrent = new Float64Array(m.ncomp);
      selCurrent = new Float64Array(m.ncomp);
   } else {
      ncompBest = statsCurrent.ncompBest,
      sensCurrent = statsCurrent.sens;
      specCurrent = statsCurrent.spec;
      effCurrent = statsCurrent.eff;
      selCurrent = statsCurrent.sel;
   }

   const Nh = pcamodel_get(m, 'cNh');
   const h0 = pcamodel_get(m, 'ch0');
   const Nq = pcamodel_get(m, 'cNq');
   const q0 = pcamodel_get(m, 'cq0');

   const sens = new Float64Array(m.ncomp);
   const spec = new Float64Array(m.ncomp);
   const sel = new Float64Array(m.ncomp);
   const eff = new Float64Array(m.ncomp);

   const sensDiff = new Float64Array(m.ncomp);
   const specDiff = new Float64Array(m.ncomp);
   const selDiff = new Float64Array(m.ncomp);
   const effDiff = new Float64Array(m.ncomp);

   const palpha = 1 - alpha / 100;
   for (let a = 0; a < m.ncomp; a++) {
      const Nha = Nh[a]
      const h0a = h0[a];
      const Nqa = Nq[a]
      const q0a = q0[a];
      const Nfa = Nha + Nqa;
      const eCrit = qchisq(palpha, Nfa);
      let q, h;

      // compute sensitivity
      q = pcares_get(rTarget, 'qdist', a + 1);
      h = pcares_get(rTarget, 'hdist', a + 1);
      let sens_v = 0;
      for (let i = 0; i < rTarget.nrows; i++) {
         const f = h[i] / h0a * Nha + q[i] / q0a * Nqa;
         sens_v += (f < eCrit);
      }
      sens_v = sens_v / rTarget.nrows
      sens[a] = sens_v;

      sensDiff[a] = sens[a] - sensCurrent[ncompBest ? (ncompBest - 1) : a];

      // compute specificity
      q = pcares_get(rAlt, 'qdist', a + 1);
      h = pcares_get(rAlt, 'hdist', a + 1);
      const f_vec = Array(rAlt.nrows);
      let spec_v = 0;
      for (let i = 0; i < rAlt.nrows; i++) {
         const f = h[i] / h0a * Nha + q[i] / q0a * Nqa;
         spec_v += (f >= eCrit);
         f_vec[i] = f;
      }
      spec_v = spec_v / rAlt.nrows
      spec[a] = spec_v;
      sel[a] = getSelectivity(f_vec, eCrit, Nfa);
      specDiff[a] = spec[a] - specCurrent[ncompBest ? (ncompBest - 1) : a];
      selDiff[a] = sel[a] - selCurrent[ncompBest ? (ncompBest - 1) : a];

      // compute efficiency
      eff[a] = Math.sqrt(sens_v * spec_v);
      effDiff[a] = eff[a] - effCurrent[ncompBest ? (ncompBest - 1) : a];
   }

   return {
      alpha: alpha,
      ncomp: m.ncomp,
      sens: {diff: sensDiff, val: sens},
      spec: {diff: specDiff, val: spec},
      sel: {diff: selDiff, val: sel},
      eff: {diff: effDiff, val: eff}
   };
}


/**
 * Create a PCA model, applies it to the PV-set and alternative set and compute main statistics.
 *
 * @param {Matrix} X_train - matrix with train set values.
 * @param {Matrix} X_pv - matrix with PV-set values.
 * @param {Matrix} X_alt - matrix with alternative set values.
 * @param {Array} varInd - array with excluded variables
 * @param {Number} ncomp - maximum number of components to use.
 * @param {boolean} center - logical, center or not
 * @param {boolean} scale - logical, scale or not
 * @param {Object} statsCompare - JSON with statistics of current model to compare the new results with.
 *
 * @returns {Object} JSON with outcomes  same as `getStat()` method.
 */
export async function getOutcomes(WASM,datasets, varInd, ncomp, center, scale, alpha, statsCompare) {
   return WASM()
      .then(Module => {
         const m = pca_fit(Module, datasets.cal.values, ncomp, center, scale, [], varInd);
         const res_target = pca_predict(Module, m, datasets.pv.values);
         const res_alt = pca_predict(Module, m, datasets.alt.values);
         return getStat(m, res_target, res_alt, statsCompare, alpha);
      })
      .catch(e =>{
         console.error(e)
      });
}


/**
 * Computes variable selection parameters based on total number of variables and interval size.
 * @param {Number} nVars - total number of variables.
 * @param {Number} nInt - number of intervals.
 * @param {Number} intSize - size of intervals (number of variables inside).
 * @param {Array} varValues - array with variable values.
 *
 * @returns {Object} JSON with interval parameters, including indices of left and right boundaries,
 * coordinates of left boundary shifted for making nice interval plots, etc.
 */
export function getVarselParams(nVars, nInt, intSize, varValues) {

   if (!intSize && !nInt) {
      throw new Error('One of the two arguments "nInt" or "intSize" must be specified.');
   }

   if (nInt) {
      intSize = Math.round(nVars / nInt);
   } else {
      nInt = Math.round(nVars / intSize);
   }

   if (!Number.isInteger(nInt) || nInt < 2 || nInt > nVars) {
      throw new Error('Wrong value for "nInt" or "intSize" arguments.');
   }

   if (!Number.isInteger(intSize) || intSize < 1 || intSize > nVars / 2) {
      throw new Error('Wrong value for "nInt" or "intSize" arguments.');
   }

   // arrays with left and right indices of each interval
   const intLeftIndices = Array(nInt);
   const intRightIndices = Array(nInt);

   // arrays of bar properties to visualize the intervals on plots
   const mid = Array(nInt);
   const left = Array(nInt);
   const width = Array(nInt);

   // how many whole intervals can be fitted and what is the reminder
   const varRem = nVars % nInt;
   const nVarRem = Math.trunc(nVars / nInt);

   let i, leftInd, rightInd;

   // loop for computing indices of intervals
   if (nInt === nVars) {
      // one variable intervals
      intSize = 1;
      for (i = 0; i < nInt; i++) {
         intLeftIndices[i] = i;
         intRightIndices[i] = i;
      }
   } else if (varRem === 0) {
      // all intervals of equal size
      for (i = 0; i < nInt; i++) {
         leftInd = i * nVarRem;
         intLeftIndices[i] = leftInd;
         intRightIndices[i] = leftInd + nVarRem - 1;
      }
   } else {
      // Loop for intervals with an extra element
      let currentInd = 0;

      // Loop for intervals with an extra element
      for (i = 0; i < varRem; i++) {
         intLeftIndices[i] = currentInd;
         currentInd += (nVarRem + 1);
         intRightIndices[i] = currentInd - 1;
      }

      // Loop for intervals with the minimum number of elements
      for (i = varRem; i < nInt; i++) {
         intLeftIndices[i] = currentInd;
         currentInd += nVarRem;
         intRightIndices[i] = currentInd - 1;
      }
   }

   let leftIndNext, rightIndPrev, leftVal, rightVal, leftValNext, rightValPrev, leftMargin, rightMargin;
   let limX = [0, 1];

   // loop to computer other parameters
   for (i = 0; i < nInt; i++) {

      // indices and values of current interval
      leftInd = intLeftIndices[i];                       // index of left boundary of i-th interval
      leftVal = varValues[leftInd];                      // value corresponding to left boundary

      rightInd = intRightIndices[i];                     // index of right boundary of i-th interval
      rightVal = varValues[rightInd]                     // value corresponding to right boundary


      // if this is not the first interval, compute left margin as half distance between this and previous interval
      if (i > 0) {
         rightIndPrev = intRightIndices[i - 1];
         rightValPrev = varValues[rightIndPrev];
         leftMargin = (leftVal - rightValPrev) / 2;
      }

      // if this is not the last interval, compute right margin as half distance between this and next interval
      if (i < nInt - 1) {
         leftIndNext = intLeftIndices[i + 1];
         leftValNext = varValues[leftIndNext];
         rightMargin = (leftValNext - rightVal) / 2;
      }

      // if this is the first interval use right margin as left
      if (i === 0) {
         leftMargin = rightMargin;
         limX[0] = leftVal - 2 * leftMargin;
      }

      // if this is the last interval use left margin as right
      if (i === nInt - 1) {
         leftMargin = rightMargin;
         limX[1] = rightVal + 2 * rightMargin;
      }

      // compute interval bar properties
      left[i] = leftVal - leftMargin;
      width[i] = Math.abs(rightVal - leftVal + leftMargin + rightMargin);
      mid[i] = (leftVal + rightVal) / 2;
   }

   return {
      nVars,
      nInt,
      intSize,
      intLeftIndices,
      intRightIndices,
      limX,
      left,
      width,
      mid
   };
}


/**
 * Finds best combination of number of intervals for given number of variables.
 *
 * @param {Number} nVars - number of variables in the dataset.
 *
 * @returns {Array} array with number of intervals options.
 */
export function getIntNumOptions(nVars) {

   // find exact match
   const v1 = [4, 6, 7, 8, 9].filter(v =>  v <= nVars && nVars % v === 0);

   // find other good options
   let v2;
   if (nVars >= 10 && nVars < 20) {
      v2 = [nVars];
   } else if (nVars >= 20 && nVars < 30) {
      v2 = [10, nVars];
   } else if (nVars >= 30 && nVars <= 50) {
      v2 = [10, 20, nVars];
   } else if (nVars > 50 && nVars < 70) {
      v2 = [10, 20, 30];
   } else if (nVars >= 70 && nVars < 90) {
      v2 = [10, 20, 30, 40];
   } else if (nVars >= 90) {
      v2 = [10, 20, 30, 40, 50];
   } else if (nVars === 5) {
      v2 = [5];
   } else {
      v2 = [];
   }

   let v = [...v1, ...v2];
   return v.sort((a, b) => a - b);
}