<script>
   /**************************************************************/
   /* AppPlots component for plots                               */
   /* -------------------------------                            */
   /* show main DD-SIMCA LOVE plot and enables interaction       */
   /* and excluding intervals.                                   */
   /**************************************************************/

	import { createEventDispatcher } from 'svelte';

   // local plot and plot controls components
   import OutcomesPlot from '../shared/OutcomesPlot.svelte';
   import SelectedIntervalPlot from '../shared/SelectedIntervalPlot.svelte';
   import OutcomesPlotControls from '../shared/OutcomesPlotControls.svelte';

   // import control components
   import Container from '../../../shared/controls/Container.svelte';
   import Button from '../../../shared/controls/Button.svelte';
   import ProgressPlot from '../shared/ProgressPlot.svelte';


   /*****************************************/
   /* Component parameters                  */
   /*****************************************/

   // inputs
   export let datasets;
   export let outcomes;
   export let varselparams;
   export let statsGlobal;
   export let statsCurrent;
   export let statsProgress;
   export let intExcluded;
   export let intSelected = -1;

   // output
   export let ncomp;

   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // outcomes names and keys
   const fomNames = {
      'sens': 'sensitivity',
      'spec': 'specificity',
      'eff': 'efficiency',
      'sel': 'selectivity'
   };

   const fomKeys = Object.keys(fomNames);

   // colors
   const seriesColors = {
      'global': '#404040',
      'best': '#2679B2',
      'selected': '#D22C2F'
   }

   // index of selected object and variable
   let oldIntExcluded = intExcluded;


   // selected plot number and its parameters
   let params = null;

   // event dispatcher
   const dispatch = createEventDispatcher();


   // function to dispatch event when outlier is marked
   function markExcluded(el) {
      dispatch('setexcluded', {id: intSelected});
   }

   // reset selection if interval was excluded
   $: if (oldIntExcluded !== intExcluded) {
      oldIntExcluded = intExcluded;
      intSelected = -1;
   }

   // reactive expression to follow changes in ncomp
   $: if (params && params.ncomp !== ncomp) {
      ncomp = params.ncomp;
   }
</script>

<div class="plot-container">

   <!-- column with current plot -->

   <div class="plot-area">
      {#if params}
         {#if params.plotType === 'statistics'}
         <SelectedIntervalPlot
            selected={intSelected}
            {statsGlobal}
            {statsCurrent}
            {outcomes}
            {params}
            {seriesColors}
         />
         {:else if params.plotType === 'progress'}
         <ProgressPlot
            selected={intSelected}
            {fomNames}
            {fomKeys}
            {statsProgress}
            {seriesColors}
            {outcomes}
            {ncomp}
            {params}
         />
         {:else}
         <OutcomesPlot
            bind:selected={intSelected}
            {fomNames}
            {fomKeys}
            {statsGlobal}
            {statsCurrent}
            {varselparams}
            {outcomes}
            {datasets}
            {params}
            {intExcluded}
            {seriesColors}
         />
         {/if}
      {/if}
   </div>

   <!-- column with plot selectors and current plot settings -->
   <div class="plot-controls-area">
      <OutcomesPlotControls {outcomes} {intExcluded} bind:params={params} />
      {#if outcomes && outcomes.status === 'done' && intSelected >= 0}
      <Container label="Selected ({intSelected + 1})">
         <span class="buttons button-outlier">
         <Button on:click={markExcluded}>exclude</Button>
         </span>
      </Container>
      {/if}
   </div>
</div>


<style>
   .plot-controls-area > :global(.control-element:has(.button-outlier)) {
      background: #fff0e8;
   }

   .button-outlier  :global(span:last-child > button) {
      color: white;
      border-color: crimson;
      background: crimson;
   }

   .button-outlier  :global(span:last-child > button:hover) {
      background: #DC143C20;
      border-color: #DC143C80;
      color: crimson;
   }

   .buttons {
      position: relative;
      box-sizing: border-box;
      display: inline-block;
      flex: 1 1 auto;
      margin: 0;
      padding: 2px 0;
      width: auto;
      border-radius: 2px;
      cursor: default;
   }

   .plot-container {
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      padding: 0;
      display: grid;
      grid-template-rows: 1fr;
      grid-template-columns: 1fr minmax(270px, 30%);
   }

   .plot-area {
      padding: 1em;
      margin-top: 0.25rem;
      border-radius: 0.25em;
      background: #fefefe;
   }

   .plot-controls-area {
      padding: 0 0.25em 0 0.5em;
   }

   :global(div.plot-container.off-screen) {
      position:absolute;
      top:0;
      left:50%;
      width: 650px;
      height: 600px;
      background: white;
      z-index: 999;
   }

   @media(min-width:1100px) {
      .plot-container {
         grid-template-columns: 1fr minmax(285px, 30%);
      }
   }

   @media(min-width:1300px) {
      .plot-container {
         grid-template-columns: 1fr max(300px, 30%);
      }
   }

   @media(min-width:1600px) {
      .plot-container {
         grid-template-columns: 1fr 400px;
      }
   }

</style>