<script>
   /**************************************************************/
   /* LOVE                                                       */
   /* -------------------------------------                      */
   /* Svelte component for LOVE variable selection               */
   /**************************************************************/

	import { createEventDispatcher } from 'svelte';

   import { Matrix } from 'mdatools/arrays';
   import { seq } from '../../../shared/utils.js';
   import { getOutcomes, getVarselParams, getIntNumOptions } from '../utils';

   import Select from '../../../shared/controls/Select.svelte';
   import Container from '../../../shared/controls/Container.svelte';
   import Button from '../../../shared/controls/Button.svelte';
   import ButtonCancel from '../../../shared/controls/ButtonCancel.svelte';
   import ButtonUndo from '../../../shared/controls/ButtonUndo.svelte';


   /*****************************************/
   /* Component parameters                  */
   /*****************************************/

   // input
   export let WASM;
   export let model;
   export let datasets;
   export let varselparams;
   export let statsCurrent;
   export let intExcluded;
   export let varsExcluded;

   // outputs
   export let outcomes;
   export let alpha = 5;


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // event dispatcher
   const dispatch = createEventDispatcher();

   // options for alpha to select from
   const alphaOptions = [10, 5, 1, 0.1];

   // initial interval parameters
   let nVars = datasets.cal.values.ncols;                               // number of variables
   let nInt = nVars <= 50 ? nVars : 10;                                 // initial number of intervals
   let varValues = datasets.cal.varvalues;                              // variable values

   // interval size options to be selected by user
   let nIntOptions = getIntNumOptions(nVars);


   // helper functions

   /**
    * Runs variable selection procedure by excluding intervals sequentially, building DD-SIMCA model
    * without the excluded interval, applying the model to datasets and storing main outcomes (FOMs).
    *
    * @param {Object} varselparams  - parameters for variable selection.
    * @param {Array} intExcluded    - array with excluded intervals.
    * @param {number} alpha         - significance level for decision.
    * @param {Object} statsCompare  - JSON with statistics to compare the results.
    *
    * The results are used to update global object 'outcomes'. The 'model' and 'datasets' are already
    * defined when this component is created and do not change reactively.
    *
    */
   async function makeSelection(varselparams, intExcluded, alpha, statsCompare) {

      // correct number of components if necessary
      // we do not take into account number of rows here because it was accounted for global model and does not change
      const nVarsNew = datasets.cal.values.ncols - varsExcluded.length - varselparams.intSize;
      const nComp = model.ncomp > nVarsNew ? nVarsNew : model.ncomp;

      // get total number of intervals
      const nInt = varselparams.nInt;

      // prepare matrices for fom values and differences
      const sens = Matrix.zeros(nComp, nInt);
      const spec = Matrix.zeros(nComp, nInt);
      const eff = Matrix.zeros(nComp, nInt);
      const sel = Matrix.zeros(nComp, nInt);

      // prepare matrices for fom differences
      const sensDiff = Matrix.zeros(nComp, nInt);
      const specDiff = Matrix.zeros(nComp, nInt);
      const effDiff = Matrix.zeros(nComp, nInt);
      const selDiff = Matrix.zeros(nComp, nInt);

      // loop over all intervals
      for (let i = 0; i < nInt; i++) {

         // in the interval is already excluded - skip it
         if (intExcluded.length > 0 && intExcluded.includes(i)) continue;

         // set status "running" so other components know
         outcomes = {
            status: 'running',
            curInt: i,
         };

         // excluded i-th interval
         const intVarInd = seq(varselparams.intLeftIndices[i], varselparams.intRightIndices[i])
         const varInd = varselparams.intSize === 1 ? [...varsExcluded, i] : varsExcluded.concat(intVarInd);

         // get statistics
         const stat = await getOutcomes(WASM, datasets, varInd, nComp, model.center, model.scale, alpha, statsCompare);

         // save statistics to matrices
         sens.v.set(stat.sens.val, i * nComp);
         spec.v.set(stat.spec.val, i * nComp);
         eff.v.set(stat.eff.val, i * nComp);
         sel.v.set(stat.sel.val, i * nComp);

         // save differences to matrices
         sensDiff.v.set(stat.sens.diff, i * nComp);
         specDiff.v.set(stat.spec.diff, i * nComp);
         effDiff.v.set(stat.eff.diff, i * nComp);
         selDiff.v.set(stat.sel.diff, i * nComp);
      }

      outcomes = {
         status: 'done',
         ncomp: nComp,
         sens: {val: sens.t(), diff: sensDiff.t()},
         spec: {val: spec.t(), diff: specDiff.t()},
         eff: {val: eff.t(), diff: effDiff.t()},
         sel: {val: sel.t(), diff: selDiff.t()},
      };
   }


   // if array with excluded intervals has been changed, reset outcomes
   let oldIntExcluded = intExcluded;
   $: if (oldIntExcluded !== intExcluded) {
      oldIntExcluded = intExcluded;
      outcomes = null;
   }

   // reactive expression to compute interval parameters
   $: if (nInt > 0) {
      outcomes = null;
      varselparams = getVarselParams(nVars, nInt, null, varValues);
   } else {
      varselparams = null;
   }

</script>

<Container label="">
   <p class="title">Variable selection settings</p>
</Container>

{#if intExcluded.length === 0 && outcomes === null}
<Container label='Alpha, %:'>
   <Select id='alpha' bind:value={alpha} options={alphaOptions} />
</Container>
{:else}
<Container label='Alpha, %:'>
   <p style="padding: 0.25em; font-weight:bold;">{alpha}</p>
</Container>
{/if}

{#if intExcluded.length === 0}
<Container label='N. of intervals:'>
   <Select id='nInt' bind:value={nInt} options={nIntOptions} />
</Container>
{:else}
<Container label='N. of intervals:'>
   <p style="padding: 0.25em; font-weight:bold;">{nInt}</p>
</Container>
<Container label="Intervals">
   <div class="container">
   <p style="padding: 0.25em; font-weight:bold;">{intExcluded.length} out, {varselparams.nInt - intExcluded.length} in</p>
   {#if !outcomes || outcomes.status !== 'running'}
   {#if intExcluded.length >= 2}
   <ButtonUndo on:click={() => dispatch('undoexcluded')} />
   {/if}
   <ButtonCancel on:click={() => dispatch('resetexcluded')} />
   {/if}
   </div>
</Container>
{/if}


{#if outcomes === null || (outcomes.status === 'done' && intExcluded.length === 0)}

<Container label='&nbsp;'>
   {#if (intExcluded.length < varselparams.nInt - 1) && (varsExcluded.length < (varselparams.nVars - 2))}
   <Button id='run' on:click={() => makeSelection(varselparams, intExcluded, alpha, statsCurrent)}>
      Run {intExcluded.length > 0 ? 'new' : ''} selection
   </Button>
   {:else}
   <p style="padding: 0.25em;color:crimson;font-weight:bold;">No intervals left for selection.</p>
   {/if}
</Container>
{/if}


